  parrafo17 = "Entre las historias de la sesin, me gust la idea de Luna de San Jorge para crear su propia caja de herramientas y la historia de Antonia. Esta ltima me reson al producirme sensaciones y recuerdos en ese momento. Al ser una historia compuesta por escenas retrospectivas de esas situaciones cotidianas que produce la sensacin de dejar de ser uno mismo por otra persona. Pero que lo aceptaba como algo normal. Ahora me daba cuenta que lo invisibilizaba en cierto grado y lo haca apropsito, lo cual me fastidi. Inicialmente, se grab el audio como una forma de registrar. Posteriormente, se acord con Antonia no usar el audio en el TFG, porque expona detalles personales en un documento de consulta pblica. El audio era opuesto al primer principio en el encuadre: lo importante son las personas.";
