  parrafo23 = "Esta preocupacin me hizo revisar el procedimiento establecido en el 2017 con la Api Graph de Facebook en la versin 2.6 que brindaba la informacin de forma ms descriptiva y similar a la estructura de datos requerida: JSON, que permita tener el contexto de los elementos iniciales con la persona autora y las reacciones de cada persona en la red social. No obstante, la versin 3.1 de la API recopila la informacin de forma desconectada entre el contexto, el elemento inicial y las reacciones al separar la autora de los mismos.";
