        "nombre": "Dominguillo",
        "descripcion": "Se pide al grupo que forme un crculo, todos de pie mirando al centro. Despus, que se inclinen en direccin al centro sin doblar la cintura, encorvarse ni levantar los talones, como la Torre de Pisa. A continuacin, que se inclinen hacia fuera. Que hagan lo mismo hacia la izquierda, hacia la derecha, siempre sin doblar la cintura ni levantar los pies. Que dibujen un crculo en el espacio con sus cuerpos, que se inclinen hacia delante, hacia la izquierda, hacia atrs, hacia la derecha, hacia el centro; luego lo mismo pero al contrario, centro, derecha, atrs, izquierda delante, y as varias veces. Un voluntario va al centro, cierra los ojos y hace lo mismo, slo que esta vez se deja caer; todos los dems deben sostenerlo con las manos, permitindole inclinarse hasta muy cerca del suelo. A continuacin deben volver a situarlo nuevamente en el centro, pero l caer en otra direccin, siendo sujetado siempre por tres compaeros, por lo menos. Al final, los compaeros pueden ayudar al protagonista con las manos a girar en crculo, en vez de llevarlo de nuevo hacia el centro. Variante Dos personas solamente y una tercera en el medio que se deje caer hacia delante y hacia atrs, siendo sostenida por los dos compaeros.",
