        "descripcion": "Comienzo por el ejercicio tericamente ms fcil de hacer pero que, debido a las mecanizaciones psicolgicas y fsicas, se vuelve de difcil ejecucin en la prctica. Como no hay necesidad de preparacin, los no actores se entregan sin miedo; y si se les advierte que es casi imposible realizarlo bien, no tendrn miedo a conseguirlo y se sentirn libres para intentarlo. Se pide que hagan un crculo con la mano derecha, grande o pequeo, como puedan: es fcil, y todo el mundo lo hace. Se pide despus que hagan una cruz con la mano izquierda: es an ms fcil y todos lo logran. Se pide entonces que hagan las dos cosas al mismo tiempo. Es casi imposible. En un grupo de unas treinta personas, a veces una lo logra. Difcilmente dos, y tres es un rcord. Tambin servir cualquier figura diferente para cada mano, adems del crculo y la cruz. Variante Se pide que hagan un crculo con el pie derecho, durante un minuto. Que olviden el pie y sigan haciendo el crculo. Despus, que escriban sus nombres en el aire, con la mano derecha. Es casi imposible. El pie tiende a seguir a la mano y a escribir el mismo nombre tambin. Para facilitar el ejercicio, se intenta hacer un crculo con el pie izquierdo y escribir con la mano derecha. Es ms fcil, y algunas personas a veces lo consiguen.",
