        "nombre": "Espacio, el volumen y el territorio",
        "descripcion": "El espacio es infinito, mi cuerpo es finito. Mi cuerpo finito tiene un volumen que ocupa cierto espacio. Alrededor de mi cuerpo, sin embargo, est mi territorio, que es subjetivo. Si alguien se acerca a veinte centmetros de mis ojos, aunque no me toque, invade mi territorio, que se extiende ms all de mi cuerpo; en cada cultura, el concepto de territorio puede variar. Una mujer est sentada en un vagn de metro repleto de gente. Todos los asientos estn ocupados, salvo el que est a su lado. Un hombre entra, ve el asiento vaco junto a ella y se sienta: el territorio de la mujer no ha sido invadido. La misma mujer est sentada en el mismo asiento, y todo el vagn est vaco. El mismo hombre entra y se sienta al lado de ella: est invadiendo su territorio. En este ejercicio-foro, los espec-actores sustituyen a la mujer y muestran diferentes maneras de recuperar su territorio. Otros ejemplos: un hombre en una cabina telefnica: si en la fila que est detrs de l las personas mantienen cierta distancia, no estarn invadiendo su territorio; si estn muy cerca, sin embargo, oyendo lo que dice, se habr configurado la invasin. Lo mismo ocurrir en un banco, con las personas haciendo cola detrs de alguien que est en la caja; o con una pareja que se besa en un parque, con alguien sentado frente a ellos observando lo que hacen. Peor an: si algunos hombres hacen cola detrs del hombre que besa a su novia...En ninguno de esos casos se llega a tocar los cuerpos de los protagonistas, pero sus territorios subjetivos han sido incuestionablemente invadidos. Qu podemos hacer? Se realiza el foro.",
