        "Id": 184,
        "nombre": "Carrusel de las imgenes",
        "descripcion": "Pequea tcnica, modesta pero muy til cuando se trabaja con grupos pequeos. Primera etapa: la improvisacin Como de costumbre, a partir de una propuesta del protagonista. Segunda etapa: el carrusel Supongamos que trabajamos con un grupo de cinco, el participante n 1 es el protagonista, el n 2 el antagonista. Los otros tres observan. Una vez terminada la escena, el protagonista ocupa el lugar del antagonista, y el participante n 3 se convierte en protagonista. Luego, el verdadero protagonista, que ahora interpreta al antagonista, sale; el n 3 ocupa su lugar para mostrar cmo ha visto al verdadero antagonista, y el n 4 ocupa el lugar del protagonista; y as sucesivamente hasta que todos los participantes hayan actuado, durante toda la escena o parte de ella, tanto un personaje como el otro. El carrusel puede hacerse tambin en el otro sentido: el primero en salir es el protagonista, cuyo papel ser interpretado por el antagonista, reemplazado a continuacin por el participante n 3, continuando la rotacin en ese sentido. Tercera etapa: ping-pong Despus de la primera improvisacin, todos pueden intervenir sustituyendo a uno u otro, el nmero de veces que deseen y en los momentos juzgados oportunos. El animador intervendr solamente para garantizar que cada uno disponga del tiempo suficiente para desarrollar la accin, como protagonista o como antagonista. Primera variante Esta variante consiste en la formacin de dos equipos despus de la improvisacin: cada equipo se encargar de sustituir slo al uno o al otro, como en un juego, un partido, un combate. Segunda variante Los actores no hablan y utilizan su cuerpo para dialogar. No se trata de mimar, sino de utilizar el cuerpo de manera expresiva, somatizando las emociones: una comunicacin ssmica. Tercera variante : Esta variante se ha transformado poco a poco en tcnica de pleno derecho: 1. cuatro actores construyen su imagen de opresin y se sitan en ella como oprimidos; sus testigos los observan a cmara lenta, tratan de librarse de su opresin, y los dems personajes presentes se comportan segn lo que crean que representan: opresores o aliados; 2. cada actor-oprimido sale de su imagen y se integra en la siguiente, en el lugar del oprimido (en una opresin distinta de la suya); lo siguen sus testigos. Se toma un minuto para sentir esa nueva imagen y despus se procede como antes; 3. despus de haber recorrido las otras tres imgenes, los actores regresan a la imagen que crearon. Los testigos les dicen entonces lo que han observado en su comportamiento en las dems imgenes, y los actores de su propia imagen les dirn lo que los otros actores-oprimidos han hecho en ella; 4. se prueba una vez ms. Se cierra la tcnica con un intercambio de ideas entre todos los participantes.",
