        "descripcion": "Esta tcnica, de naturaleza prospectiva, es sencilla y resulta til para la movilizacin rpida de un grupo y la comprobacin esttica de los elementos comunes a su miembros. Primera etapa: el juego El animador pide al grupo que camine por la sala. De vez en cuando, dar tres tipos de consignas: 1) el horario; 2) la imagen; 3) la accin. El horario ser una secuencia sucesiva de momentos clave. A veces, se determinar la hora con precisin: medioda, las catorce horas, las diecisis, las veintids, medianoche, las tres de la maana, las ocho. En otras ocasiones, quedar imprecisa: el comienzo de la tarde, la llegada a casa despus del trabajo, despus de una cena, despus de una fiesta, etc. Cuando al animador le parezca importante, podr especificar el da de la semana: sbado por la noche, domingo por la tarde. Tambin puede evocar fechas especiales: las seis de la tarde un da de elecciones cuando van a cerrar las urnas, la maana del da de nuestro cumpleaos, quince segundos antes de medianoche el 31 de diciembre. Tambin puede proponer fechas excepcionales: el da de la muerte de un presidente en ejercicio, el 7 de septiembre en Brasil, el 14 de julio en Francia, el 11 de septiembre en Nueva York, cuando el atentado contra las Torres Gemelas. El animador pedir tambin, preferentemente hacia el final del juego, que formen la imagen de la cosa ms desagradable que tienen que hacer con cierta regularidad, sin importar la hora o el da de la semana, y luego la de la segunda cosa ms desagradable, para formar a continuacin la imagen de la cosa ms agradable y la de la segunda cosa ms agradable. La repeticin promueve la sinceridad. El animador anuncia la hora y los participantes se preparan. Dir imagen y los participantes formarn, simultneamente, la imagen congelada de lo que habitualmente hacen ese da a esa hora. A continuacin, el animador dir accin y los participantes comenzarn un dilogo con los personajes imaginados, con los que habitualmente estn en contacto ese da a esa hora. Cada actor quedar circunscrito a su propio mundo particular, sin contacto con los dems, y deber mostrar todos los movimientos de su cuerpo: las manos, las piernas, la boca, los dedos. Todos los movimientos que tienden a repetirse a cualquier hora del da. Esas repeticiones que conllevan la mecanizacin del cuerpo. Segunda etapa: el debate El director instaura el debate sobre las diferencias ocurridas y otros temas: en qu momento se han sentido al mximo de su energa? En qu momento disminuy sta? Qu momentos han sido los ms agitados? Cundo se sintieron ms mviles? Y ms tranquilos? Qu relaciones han establecido con los dems personajes (imaginarios)? Y con animales? Con objetos? Telfono, ordenador, vasos y platos, las mascotas, los amigos, la televisin? En qu momento han iniciado una accin por deseo propio y cundo han obedecido a obligaciones? Cundo se han sentido oprimidos y cundo felices? Cundo se han sentido iguales que los dems y cundo se han sentido originales?",
