		 "Id": 191,
		 "nombre": "Gran juego del poder",
 		"descripcion": "Una mesa, seis sillas colocadas junto a la mesa y, encima de sta, una botella. Se invita a los participantes a que, por turno, acomode los objetos de manera que una de las sillas alcance una posicin superior, ms fuerte, de mayor evidencia o mayor poder, en relacin con las dems, la mesa y la botella. Cualquiera de esos objetos puede ser movido y colocado sobre otros, o al lado, o en cualquier lugar, pero ninguno llevado fuera del espacio. El grupo trabajar, sin interrupcin, un gran nmero de variaciones en las estructuras posibles, intentando comprobar cmo una estructura espacial contiene puntos fuertes y dbiles: se trata siempre de una estructura de poder. En cualquier lugar donde estemos, vivimos siempre en estructuras espaciales de poder. Cuando vamos al banco, si entramos en la cola del cajero, tenemos muy poco poder; si nos sentamos a la mesa con el gerente, crece nuestro poder; si el gerente, para colmo, tiene una sala reservada, nuestro poder ser an mayor: nos ha recibido el jefe. Estructuras espaciales de poder existen en todas partes: en el aula, en la iglesia y hasta dentro de casa. Dnde est el lugar del padre?: cerca de la nevera o cerca del televisor, o presidiendo la mesa? Y el de la madre?: cerca de la puerta de la cocina? Y el de cada hijo? Muchas veces, los nios pelean para conquistar tal o cual lugar en la mesa: estn luchando por el poder, no por el lugar, porque la comida es la misma... Despus de cada organizacin del espacio, cada uno debe decir por qu siente que esta o la otra es ms fuerte, ms poderosa: claro que no hace falta llegar a un acuerdo: la pluralidad de sentimientos y opiniones se vuelve estimulante y creativa. Se elige despus una estructura determinada, que puede ser una de las que han sido creadas u otra cualquiera, como, por ejemplo, una silla detrs de la mesa frente a dos pares de sillas y una ms en el medio, atrs, y se pide a los actores que, uno a uno, entren en esa estructura espacial y coloquen su propio cuerpo en una posicin donde reciba el mximo de poder de esa estructura: sentado en una de las sillas, de pie o debajo de la mesa, dnde? cuando se llega a la eleccin de una de las posiciones, se invita a los dems actores, siempre uno a uno, a que entren en la estructura donde ya est el actor que ha formado la imagen ms poderosa y, colocando sus propios cuerpos en algn lugar, intenten conquistar para s el poder. Cuando ste sea el caso, los dos actores se quedan inmviles en sus posiciones, y un tercero intenta conquistar para s el poder que el segundo ha arrebatado. Despus, siempre que alguien conquista ese poder, permanece en escena, aumentando el nmero de personas que participan de la estructura espacial. Por poder se entiende aqu volverse el centro de atencin, como la imagen de Kennedy muerto en su coche es la imagen ms poderosa de la foto, o la del Papa tras recibir un disparo en su automvil: uno est muerto, otro gravemente herido, pero ambos atraen la atencin de quien mira las fotos: ambos tienen poder! Este juego sensibiliza a los participantes porque permite que tomen conciencia de que ninguna estructura espacial es inocente: todas tienen un significado y una desigual distribucin de poder: que se aproveche o no es otro asunto...",
