 		"descripcion": "Una de las tcnicas ms intensas y complejas del arsenal del Teatro del Oprimido. Debe utilizarse cuando el protagonista est realmente dispuesto a usarla, cuando conoce todas las etapas. No ha de obligarse al protagonista a hacerla entera; el animador debe decirle claramente que puede abandonarla durante el desarrollo de la misma, en el momento en que desee hacerlo. Primera etapa: la improvisacin. Una improvisacin normal, en la que el protagonista ede quin encarna cada papel y los actores interpretan personaje dentro de las consignas fijadas por l, recuerdo a su sensibilidad y experiencia para completarlas, tcnica es ms eficaz cuando la escena slo consta del protagonista y un antagonista, y ser ms til cuando se que una situacin en la que el propio protagonista no 187 comprenda lo que le sucede, o presente una cierta ona  dad hacia s mismo. Segunda etapa: la formacin de imgenes. Los participantes tendrn que dejarse estimular por la improvisacin, no como espectadores, riendo o aplaudiendo con una actitud de consumidores pasivos, sino que al contrario, lo ideal sera que observaran el ms estricto silencio. Como espect-actores dispuestos a intervenir, dejndose penetrar por los estmulos externos y permitiendo que estos den forma a su cuerpo e informen su sensibilidad. Tras la improvisacin se invita a los espect-actores a hacer imgenes de cmo han sentido las acciones del protagonista y el antagonista. Imgenes que han de ser el producto de la percepcin de un detalle oculto, disimulado por la actitud general de uno y otro, y que fragiliza al protagonista, dejndolo vulnerable y dando ms fuerza a las armas del antagonista. Puede suceder que la escena muestre una situacin que no constituya claramente una relacin evidente de oprimido-opresor. En ese caso, las imgenes sern lo que los participantes hayan conseguido desvelar de los elementos escondidos. Las imgenes no sern nunca realistas: no se trata de reproducir lo que todos pueden ver, sino slo lo que cada participante haya sentido individualmente, al adoptar voluntariamente una actitud de simpata con uno u otro y haberse puesto en relacin con ellos por identidad o reconocimiento. Las imgenes sern metafricas, pleonsticas, surrealistas, expresionistas, magnificadas, deformadas, en fin, todo, con tal de que sean verdaderas. No realistas: verdaderas, sentidas, vividas. Tenemos un compromiso real, no con el realismo, que con frecuencia esconde ms que revela la realidad. Se hacen cinco imgenes de cada personaje. Tercera etapa: la formacin de parejas. Una vez hechas las imgenes, cada actor-imagen buscar su complemento en el grupo opuesto, formando parejas de imgenes complementarias. La eleccin complementaria es aleatoria, sin una razn clara, y pueden hacerla los actores o los dems participantes. Cuarta etapa: las reimprovisaciones. Hechas las parejas, cada una dispondr de un tiempo bastante corto para volver a improvisar la escena, y cada actor mantendr la imagen tal como la haba mostrado. Por ejemplo, si una de las imgenes mostraba al protagonista en la actitud de un boxeador, el actor deber mantener esa actitud durante la improvisacin. Puede moverse pero no puede modificar la imagen, que funcionar como un filtro: lo que el actor diga quedar connotado por esa imagen, que es la visualizacin magnificada de un elemento del comportamiento del personaje. En la reimprovisacin los actores podrn decir no slo las frases pronunciadas durante la improvisacin original, sino tambin pensamientos y acciones coherentes con la escena original, rplicas que constituyen la exteriorizacin, monlogo interior. La reimprovisacin mezcla memoria e imaginacin. El protagonista y el antagonista observan atentamente las improvisaciones, pues tendrn que volver a escena. Quinta etapa: el protagonista asume las imgenes. Terminada la etapa precedente, las parejas vuelven a escena alternativamente para improvisar una vez ms la misma situacin, esta vez con las mismas palabras y acciones que la primera vez, los mismos movimientos; pero ahora el protagonista se colocar al lado de su imagen, asumindola y repitiendo lo que el actor diga o haga, como un eco. El actor-imagen, al haber sido mimetizado por un aspecto, un detalle del comportamiento del protagonista, ha creado una imagen amplificada de ese detalle; en esta etapa, se produce el proceso inverso: el protagonista va a ser mimetizado por la imagen del mimetismo anterior, en otras palabras, mimetizado por s mismo; va a magnificar un detalle que se supone que es suyo, y ese detalle se convertir en el todo. No es una caricatura: la caricatura exagera lo que ya es exagerado; aqu se trata de descubrir lo que est escondido y desvelarlo. Transcurrido un cierto tiempo, el animador dir sal! y el actor-imagen saldr de escena, dejando al protagonista a solas con el actor-imagen del antagonista. Durante un rato, el protagonista deber mantener la misma imagen y proseguir la escena segn el mismo comportamiento del actor-imagen, como si fuera l. A continuacin, el animador dir puedes cambiar!, y, si el protagonista est satisfecho con la imagen, la mantendr y continuar la improvisacin, libre ahora de no limitarse a seguir reproduciendo lo que el actor-imagen ha hecho antes que l. Si, por el contrario, piensa que la imagen no le conviene, que lo confina en un rol que no desea, podr hacer una lenta metamorfosis, transformndose en una imagen muy distinta, opuesta, que a su juicio le sera ms til para afrontar la situacin: la imagen de lo que querra ser. Se repite este proceso con cada pareja: en un primer momento, el protagonista reproducir siempre la imagen y el comportamiento del actor, continundolo cuando ste sale de escena, para finalmente poder elegir o continuar igual o cambiar al extremo opuesto. Sexta etapa: el protagonista afronta simultneamente todas las imgenes del antagonista. El protagonista vuelve a escena, ahora solo, y afronta todas las imgenes del antagonista juntas, que improvisarn concertadamente, como si formaran un solo personaje; en realidad, son aspectos de una misma persona, el antagonista: son el producto del anlisis operado por el grupo de participantes. Las imgenes pueden hablar a la vez, pero nunca unas con otras. El protagonista puede dirigirse hacia una en particular o hacia el conjunto. En ambos casos, el animador dir al protagonista cules fueron sus movimientos, sus certezas y sus dudas en el conflicto con cada imagen. Sptima etapa: el turno del antagonista. Ahora le toca al antagonista afrontar las cinco imgenes del protagonista, en las mismas condiciones que en la etapa anterior. El protagonista observar la escena y tratar de ver cul de sus imgenes lo debilita y cmo; cul lo hace ms fuerte y por qu. Octava etapa: nueva improvisacin. El protagonista y el antagonista vuelven a escena para hacer una nueva improvisacin de la misma situacin. Si la escena mostrada es una escena de opresin, ahora el protagonista deber intentar quebrarla. Los actores que crearon las imgenes que el protagonista rechaz por juzgarlas negativas para l, se colocarn de manera que ste los vea. Si consideran que tiene una recada en direccin de las imgenes rechazadas, emitirn un sonido, una sirena de alarma: AAAAAH. Informado as de su recada, el protagonista intentar repetir la metamorfosis de las etapas anteriores. Se puede hacer lo mismo con las imgenes que s gustaron al protagonista: los actores tratarn de estimularlo cuando piensen que el protagonista va por buen camino. Novena etapa: el intercambio de ideas. El animador coordina el debate entre los participantes.",
