 		"descripcion": "Esta tcnica se basa en la hiptesis de que si bien es cierto que los rituales de la vida cotidiana nos imponen a todos una mscara adaptada a ellos, con el objetivo de disminuir los enfrentamientos entre las personas y las acciones que stas deben realizar, del mismo modo la negativa a utilizar esa mscara o la utilizacin de una mscara no conforme con l podrn desmontar el ritual o modificarlo, o tambin revelar su inadaptacin. En realidad, entre el ritual y la persona siempre se produce una lucha: la mscara es el resultado de esa lucha. Primera etapa: las improvisaciones ritualizadas El protagonista improvisar de cuatro a cinco escenas, cada una en un espacio diferente de la sala, y en las que deber mostrar opresiones suyas. Se representar a s mismo e indicar a los actores los elementos esenciales de los conflictos; los actores seguirn sus indicaciones bsicas, pero conservando su libertad de improvisacin, pues en caso contrario no sera tal... Es necesario que, en las diferentes escenas, el protagonista tenga conciencia de estarse comportando de diferentes maneras. Por ejemplo, con su mujer se comportar de manera totalmente distinta que con su psicoanalista; con su padre, diferente que con el frutero de la esquina, etc. Antes de cada escena el animador preguntar al protagonista qu QUIERE de los dems personajes y qu quieren stos de l: cada personaje ha de emplear toda su voluntad en obtener aquello que desea -en otras palabras, debe hacer acciones!-y no slo mostrar cmo es. Repito: el personaje es accin, es verbo y no adjetivo! Es querer y no slo esencia, es un yo quiero. Exteriormente, podemos convertirlo en un adjetivo, pero l nunca debe hacer esto consigo mismo. El actor debe amar (verbo) y entonces diremos que es un amante (adjetivo). No debe mostrarse amante: debe mostrar al personaje amando. Despus de cada improvisacin, el animador preguntar al grupo cmo ha visto la escena y al protagonista dentro de ella: cules fueron sus caractersticas predominantes, cul era su mscara. Los espect-actores expresarn sus opiniones con el cuerpo; varios espect-actores podrn mostrar diversas mscaras, y el grupo elegir la ms pertinente para cada escena. Una vez terminada la primera escena ritual, el protagonista pasa a la segunda; los actores de la primera se desactivan y se quedan en su zona. Activamos la segunda escena, se hace la segunda improvisacin y el animador pregunta cules fueron las caractersticas principales -y diferentes del protagonista en esta segunda improvisacin; se hablar con el cuerpo y no slo con las palabras. Se pasa a la tercera escena y as sucesivamente. Segunda etapa: el refuerzo de la mscara Acabadas las improvisaciones, el animador har un breve recordatorio de las caractersticas (mscaras) percibidas por el grupo en cada escena, para que sean magnificadas en una segunda improvisacin. Donde el protagonista mostr buen corazn, que muestre una bondad desbordante; donde fue intolerante, que sea intolerantsimo; donde fue violento, que se convierta en asesino. Ante l, el actor que encarna la imagen elegida por el grupo se mostrar an ms convincente para ayudarlo en su proceso de magnificacin. Tercera etapa: el conflicto de las mscaras contra los rituales En la tercera etapa, el animador propone que el protagonista comience una escena con la mscara correspondiente, exacerbada; a continuacin, conservando la misma mscara, improvisa todas las dems escenas, una por una, con esa mscara que evidentemente no se adapta a ellas. Los actores de cada escena reaccionarn de acuerdo con su nuevo comportamiento, y as podremos comprobar qu pasara si. ",
