            "Id": 214,
            "nombre": "Hipnotismo colombiano",
            "descripcion": "Un actor pone la mano a pocos centmetros de la cara del otro; ste, como hipnotizado, debe mantener la cara siempre a la misma distancia de la mano del hipnotizador, los dedos y el pelo, el mentn y la mueca. El lder inicia una serie de movimientos con las manos, rectos y circulares, hacia arriba y hacia abajo, hacia los lados, haciendo que el compaero ejecute con el cuerpo todas las estructuras musculares posibles, con el fin de equilibrarse y mantener la misma distancia entre la cara y la mano. La mano hipnotizadora puede cambiar para hacer, por ejemplo, que el actor hipnotizado se vea obligado a pasar entre las piernas del hipnotizador. Las manos del hipnotizador no deben hacer nunca movimientos muy rpidos, que no puedan seguirse. El hipnotizador debe ayudar a su compaero a adoptar todas las posiciones ridculas, grotescas, no usuales: son precisamente stas las que ayudan al actor a activar estructuras musculares poco usadas y a sentir mejor las ms usuales. El actor utilizar ciertos msculos olvidados de su cuerpo. Al cabo de unos minutos, se intercambian los papeles de hipnotizador e hipnotizado. Pasados unos minutos ms, los dos actores se hipnotizan mutuamente: ambos tienden la mano derecha, y cada uno obedece a la mano del otro.",
