              "descripcion": "Los actores forman un crculo; uno de ellos se coloca en el centro y realiza un movimiento cualquiera, por ms inslito que sea, acompaado de un sonido; tanto el sonido como el movimiento obedecen a un ritmo que l mismo inventa. Todos los actores lo siguen, intentando reproducir exactamente sus movimientos y sonidos dentro del ritmo, lo ms sincrnicamente posible. El actor desafa a otro, que se sita en el centro del crculo y lentamente rehace el movimiento y el sonido con otro ritmo, mientras el primer actor vuelve a la rueda. Todos ahora siguen al segundo actor, que desafa a un tercero y as sucesivamente. El que se coloca en el centro puede reproducir el ritmo corporal y musical que quiera, que ms le guste, pero que no est habituado a hacer en su vida real. No se trata de bailar o cantar algo que todos conocen. No se debe tener miedo al ridculo, a lo grotesco, a lo inslito. Si todos hiciesen el ridculo, nadie se sentira ridculo... Todos deben intentar reproducir, lo ms precisamente posible, lo que son capaces de ver y or: los mismos movimientos, la misma voz, el mismo ritmo... Si hubiese una mujer en el centro, los hombres en el crculo no deben ejecutar la versin masculina de movimiento, sino reproducir exactamente lo que estn percibiendo. Qu ocurre entonces? Cul es el mecanismo? Muy sencillo: al intentar imitar la manera de moverse, de cantar, etc., del otro comenzamos a deshacer nuestras propias mecanizaciones. Imitando a los otros, estaremos reestructurando de varias maneras diferentes (porque varios actores se situarn en el centro) nuestra propia manera de ser y de actuar. No se debe hacer una caricatura, porque nos llevara a hacer cosas diferentes, pero siempre de la misma forma rgida. Debemos intentar comprender, sentir, reproduciendo exactamente lo externo para sentir mejor lo interior de la persona situada en el centro.",
