              "descripcion": "Varias versiones posibles: a) Variante hablada: un grupo cierra los ojos, el otro forma una imagen compleja; a una seal, los ciegos abren los ojos durante tres segundos, como una cmara, y despus cada uno cuenta todo lo que ha visto y se comparan los relatos; a continuacin, el segundo grupo rehace la escena para que se comprueben errores y aciertos, b) Variante simple: todos cierran los ojos mientras un actor forma una imagen; abren los ojos tres segundos, como una cmara, y la reproducen con su propio cuerpo, en imagen especular; despus, dos personas, a continuacin tres, reproducen una imagen cada vez. c) Variante en parejas: dos parejas frente a frente; la pareja 1 cierra los ojos, la 2 forma una imagen en la que los dos integrantes se tocan; la 1 abre y cierra los ojos, reproduce la imagen con su propio cuer- po, abre los ojos y compara, d) Variante en filas: dos filas, frente a frente. La fila 1 cierra los ojos, la 2 forma una imagen compleja o una serie de pequeas imgenes con tres o cuatro actores cada una. El grupo 1 abre los ojos y los cierra. El grupo 2 vuelve a la posicin original, de pie. El grupo 1 abre los ojos y, en silencio y sin dilogo, esculpe a los actores del grupo 2 reconstruyendo la forma en la que estaban o supuestamente estaban. Si la imagen esculpida resulta igual a la imagen original, no ocurre nada; si no, cuando el director diga Ya, todos los actores de la imagen errnea debern hacer un ruido con la boca y, en cmara lenta, colocarse en la imagen correcta. Una vez ms para la errnea, con sonido, y una vez ms para la correcta, siempre con sonido, ) Variante estroboscpica: todos cierran los ojos, y un actor, en cmara lenta, realiza una serie de acciones fsicas. Durante ese perodo, el director dir Abre/Cierra cinco veces, y todos abrirn y cerrarn los ojos cinco veces. Al final, todos tendrn que reproducir las acciones que han visto en esos cinco flashes). Variante Hamlet: grupos de actores formarn imgenes de escenas de la obra (Hamlet o cualquier otra), que los dems actores deben comentar y adivinar.",
