              "descripcion": "Me gusta que todos crean en m, pero no ciegamente, porque puedo estar equivocado en lo que digo o pienso. Por ello, me gusta hacer este ejercicio. Los actores se colocan en crculo (o en ms de uno si son muchos), cierran los ojos; el director anuncia que pasar dos veces detrs de todos los actores de cada crculo y, en una de ellas, tocar slo a una persona, slo a una, en cada crculo: se ser el lder designado. A continuacin, todos deben abrir los ojos y, sin hablar, slo observndose, descubrir quin es el lder. Es sencillo: todos estarn sinceramente intentando descubrir quin est mintiendo, y el lder, mintiendo, simular que tambin busca. Despus de unos minutos, el director pide que todos levanten el brazo y, a una seal, indiquen al que creen que es el lder. El juego se hace dos veces y, en la ltima, inevitablemente todos los actores se comportan siempre de una forma muy diferente. El director pide que los actores indicados como lderes no revelen la verdad, si lo son o no. Despus de la segunda vez, el director pregunta por qu han elegido a ste o aqul, y todos darn sus razones para sospechar que el mentiroso era uno o el otro. En verdad, y all reside el truco, el mentiroso es el propio director: la primera vez, no tocar a nadie, no habr ningn lder; en la segunda tocar a todos, todos sern lderes. Este ejercicio es estupendo: como se trata de la cuarta categora (Ver todo lo que se mira), puedo asegurar que en ningn otro los actores ven tanto lo que miran como en ste. Y, al mismo tiempo, comienzan a prestar mayor atencin a lo que dice el director y no slo a obedecer mecnicamente.",
