              "descripcion": "Este juego tiene dos puntos de partida. El primero son las palabras de Bertolt Brecht: Hay muchos objetos en un solo objeto si la meta final es la revolucin, pero no habr ningn objeto en ningn objeto si no es sa la meta final. El otro punto de partida es el trabajo de Rene Magritte. Algunas de sus pinturas llevan ttulos que dificultan la identificacin de los objetos que representan: una jarra nombrada como fruto o el famoso grabado Esto no es una pipa, en el que vemos una pipa. Lo que Magritte quiso realmente decir fue que una pipa o cualquier objeto designado con un nombre inusual no son esa pipa ni ese objeto, sino obras de arte, representaciones, artes plsticas. La pipa representada no es una pipa, en efecto: basta intentar filmrsela para comprobar la verdad contenida en el ttulo, mentiroso en apariencia. Este juego es un homenaje al pintor surrealista belga. Se comienza con una botella de plstico vaca diciendo: Esto no es una botella, entonces qu ser?, y cada participante tendr derecho a usar la botella en relacin con su propio cuerpo, formando la imagen que quiera, esttica o dinmica, dando al objeto botella el sentido que quiera: un beb o una bomba, una pelota o una guitarra, un telescopio o una pastilla de jabn. Despus de la botella, se puede usar una silla. O una mesa, cualquier objeto. Un trozo de madera puede ser un arma, un bastn, una estaca, un caballo, un paraguas, una muleta, un ascensor, un puente, una pala, un mstil, una vara, un remo, un silbato, una flecha, una lanza, un violn, una aguja, muchas otras cosas menos un trozo de madera...",
